.nav-bar {
    background-color: #d2e0ec;
    display: flex;
    justify-content: center;
    gap: 2em;
    padding: 0.8em 0;
}

.nav-button {
    background: none;
    border: none;
    color: #003366;
    font-size: 1.1em;
    font-weight: bold;
    cursor: pointer;
}

.nav-button:hover,
.nav-active {
    text-decoration: underline;
}