.grade-section {
    padding: 1.5em;
    background: white;
    border-radius: 8px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

.filter-label {
    margin-right: 0.5em;
    font-weight: 500;
}

.filter-select {
    padding: 0.5em;
    margin-bottom: 1em;
    font-size: 1em;
}

.grade-table {
    width: 100%;
    border-collapse: collapse;
}

.grade-table th,
.grade-table td {
    border: 1px solid #ccc;
    padding: 0.75em;
    text-align: center;
}

.grade-table th {
    background-color: #f1f6fa;
}
